import React, { useState,useEffect } from 'react'
import './Review.css'
import Aos from 'aos'
import 'aos/dist/aos.css'
const name=['RITIKA JAIN','HITESH SAINI','AKASHAY ARORA']
const review=['It was a nice experience. The overall design of home was good. Even in small plot size, they designed spacious rooms and halls. Also, made multiple visits to the site.','Very cost-effective services provided here and the drawings are the layman’s terms of understanding.','I would really appreciate the work these guys were doing. It was really good experience they work in a very professional manner they calmly understand my requirement and portrait it on their designs.']
const l=['<','>']
const Review = () => {
    const[count,setCount]=useState(0);
    const incre = () =>{
        if(count+1<name.length)
        setCount(count+1)
        else
        setCount(0)
    }
    const decre = () =>{
        if(count-1>=0)
        setCount(count-1)
        else
        setCount(name.length-1)
    }
    useEffect(()=>{
        Aos.init({duration: 20000})
      },[])
  return (

    <div className='review flex'>
       <div className="review-text">
           <h1 data-aos='fade-up' data-aos-duration='2550'><span className="color">Genuine </span> Review <br /> of Our <span className="color">Client's</span></h1>
            <p data-aos='fade-up' data-aos-duration='2550'>we take pride in our work, but the opinions that matter most are those of our client. we've grateful for the trust they placed in us and the feedback they provide.here are some genuine reviews from our valued clients:</p>
           </div>
           <div className="review-content" data-aos='fade-up' data-aos-duration='2550'>
            <div className="box flex iv">
            <h1>{name[count]}</h1>
                <p>{review[count]}</p>
            </div>
            <div className="buttons " data-aos='fade-up' data-aos-duration='4000'>
                <button className='btn' onClick={decre}>{l[0]}</button>
                <button className='btn' onClick={incre}>{l[1]}</button>
            </div>
           </div>
    </div>
  )
}

export default Review